@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap');

* {
  font-family: 'Noto Sans KR', sans-serif;
}

a {
  text-decoration: none;
  color: #000;
}

.App {
  text-align: center;
  overflow: hidden;

  ul, li, ol {
    text-decoration: none;
    list-style: none;
  }

  #push {
    width: 100%;
    height: 45px;
    background-color: #000;
    color: #fff;
    font-size: 12px;
    position: relative;

    p {
      padding: 12px 0;
    }

    p:hover {
      cursor: pointer;
    }

    .close {
      position: absolute;
      top: 18%;
      right: 1%;
      font-weight: 600;
      font-size: 18px;
      border: none;
      background: none;
      color: #fff;
    }

  }
}

.mb-3 {
  margin-bottom: 0.4rem !important;

  & img {
    width: 120px;
    margin: 0px 5px;
  }

  & img:hover {
    cursor: pointer;
  }

}

.d-flex {
  margin: 10px 0;
}

.nav-link {
  color: black;
}

.navbar {
  padding-bottom: 0;
}

.navbar-brand {
  padding-top: 0;
}

.btn-outline-success {
  color: #3d86cf;
  border-color: #3d86cf;
}

.btn-outline-success:hover {
  color: #fff;
  background-color: #3d86cf;
  border-color: #fff;
}

.swiper-button-prev {
  color: #fff !important;
  margin: 0px 20px;
}

.swiper-button-next {
  color: #fff !important;
  margin: 0px 20px;
}

.oneslide {
  width: 100%;
  height: 42vw;
  position: relative;

  & img {
    width: 100%;
    // height: 100%;
  }

  .w_logo {
    position: absolute;
    top: 7%;
    left: 2%;
    color: #fff;
    display: flex;
    align-items: center;
    z-index: 1;

    & img {
      width: 230px;
    }

    & img:hover {
      cursor: pointer;
    }
  }

  .icons p:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  .icons img:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  .icons {
    position: absolute;
    top: 7%;
    right: 2%;
    color: #fff;
    display: flex;
    align-items: center;
    font-size: 13px;
    z-index: 1;

    p {
      margin: 0 10px;
    }

    img {
      width: 35px;
      height: 35px;
    }
  }

  .tag {
    position: absolute;
    top: 51%;
    left: 0;
    color: #fff;
    display: flex;
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    z-index: 1;
    font-size: 19px;
    font-weight: 500;
    transform: translate(50%, -50%);
  }

  .event {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #ffffff;
    display: flex;
    align-items: center;
    display: flex;
    flex-direction: column;

    h1 {
      font-size: 42px;
    }

    h4 {
      font-size: 20px;
    }

    button {
      width: 160px;
      height: 45px;
      font-size: 16px;
      font-weight: 600;
      background-color: #ffffff;
      border: none;
      border-radius: 2px;
      margin: 20px 0;
      color: #4c3422;
    }

    button:hover {
      opacity: 0.8;
    }
  }

  .event2 {
    position: absolute;
    top: 50%;
    left: 46%;
    transform: translate(-50%, -50%);
    color: #fff;
    display: flex;
    align-items: center;
    display: flex;
    flex-direction: column;

    h1 {
      font-size: 42px;
    }

    button {
      width: 160px;
      height: 45px;
      font-size: 16px;
      font-weight: 600;
      background-color: #ff97c5;
      color: #fff;
      border: none;
      border-radius: 2px;
      margin: 20px 0;
    }
    button:hover {
      opacity: 0.8;
    }
  }

  .event3 {
    position: absolute;
    top: 50%;
    left: 46%;
    transform: translate(-50%, -50%);
    color: #fff;
    display: flex;
    align-items: center;
    display: flex;
    flex-direction: column;

    h1 {
      font-size: 42px;
    }

    button {
      width: 160px;
      height: 45px;
      font-size: 16px;
      font-weight: 600;
      background-color: #ffffff;
      border: none;
      border-radius: 2px;
      margin: 20px 0;
    }
    button:hover {
      opacity: 0.8;
    }
  }

  .vd_color {
    background-color: #000;

    video {
      width: 76%;
    }
  }

}

@media (max-width:1000px){
  .oneslide {
    .tag.none {
      display: none;
    }
  }
}

@media (max-width:630px){
  .oneslide {
    .w_logo {
      display: none;
    }
    .icons {
      display: none;
    }
    .event.now,.event3.now {
      h1 {
        font-size: 20px;
      }
      button {
        display: none;
      }
    }   
    .event2.now {
      h1 {
        font-size: 20px;
        color: #ff97c5;
      }
      button {
        display: none;
      }
    } 
  }
}

@media (max-width:500px) {
  .container.banner_div {
    padding: 0;
  }
}

.container {
  padding: 55px 0 0 0;
  max-width: 1500px;


  .banner_top {
    overflow: hidden;
    margin-bottom: 0;
    margin-top: 0;
    padding-top: 100px;

    .bn_img {
      overflow: hidden;
      position: relative;

      & img {
        transition: all 0.2s linear;
      }

      & img:hover {
        transform: scale(1.1);
        opacity: 0.7;
        cursor: pointer;
      }
    }

    .list,
    .list1,
    .list2,
    .list3 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: #fff;
      font-size: 32px;
      display: inline-block;
    }

    .bn_img:hover p::after {
      transform: scaleX(1);
    }

    .list:after,
    .list1:after,
    .list2:after,
    .list3:after {
      transform-origin: 0% 50%;
    }

    p:after {
      display: block;
      content: '';
      border-bottom: solid 3px #fff;
      transform: scaleX(0);
      transition: transform 250ms ease-in-out;
    }

    .list:hover,
    .list1:hover,
    .list2:hover,
    .list3:hover {
      cursor: pointer;
    } 
  }

  .banner_bottom {
    overflow: hidden;
    margin-bottom: 20px;
    margin-top: 0;

    .bn_img {
      overflow: hidden;
      position: relative;

      & img {
        transition: all 0.2s linear;
      }

      & img:hover {
        transform: scale(1.1);
        opacity: 0.7;
        cursor: pointer;
      }
    }

    .list,
    .list1,
    .list2,
    .list3 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: #fff;
      font-size: 32px;
      display: inline-block;
    }

    .bn_img:hover p::after {
      transform: scaleX(1);
    }

    .list:after,
    .list1:after,
    .list2:after,
    .list3:after {
      transform-origin: 0% 50%;
    }

    p:after {
      display: block;
      content: '';
      border-bottom: solid 3px #fff;
      transform: scaleX(0);
      transition: transform 250ms ease-in-out;
    }

    .list:hover,
    .list1:hover,
    .list2:hover,
    .list3:hover {
      cursor: pointer;
    } 
  }


}

.row>* {
  padding-left: 0;
  padding-right: 0;

}

