.sub_top {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 0;

    .col-md-6 {
        display: flex;
        justify-content: center;

        .category {
            font-size: 14px;
            color: #b2b2b2;
            padding: 17px 0 0 0;

            span {
                color: #000;
            }
        }

        .top_name {
            margin-top: 30px;
        }
    }

    .iconz p:hover {
        opacity: 0.8;
        cursor: pointer;
    }

    .iconz img:hover {
        opacity: 0.8;
        cursor: pointer;
    }

    .iconz {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 13px;
        z-index: 1;

        p {
            margin: 0 10px;
        }

        img {
            width: 33px;
            height: 33px;
        }
    }
}

@media (max-width:430px) {
    .sub_slider.sub_con {
        .rowbox.row {
            .two_slide.col-md-6 {
                .swiper.sub_slide {
                        .swiper-button-next {
                          display: none;
                        }    
                        .swiper-button-prev {
                            display: none;
                          }            
                }
            }

            .col-md-5.textbox {
                .box3 {
                    .colorchip.mr-5 {
                        width: 62%;
                    }
                    .btns {
                        .size.wd {
                            width: 24%;
                            margin-left: 2px;
                        }
                        .btn {
                            width: 45px;
                        }
                        .btn2 {
                            width: 50px;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width:510px) {
    .sub_slider.sub_con {
        .rowbox.row {
            .two_slide.col-md-6 {
                .swiper.swiper_2 {
                    width: 18% !important;
                    height: 320px !important;
                }
                .swiper-button-next {
                    display: none;
                  }    
                  .swiper-button-prev {
                      display: none;
                    }   
            }
            .col-md-5.textbox {
                .box3 {
                    .colorchip.mr-5 {
                        width: 62%;
                    }
                }
            }
        }
    }
}


@media (max-width:682px) {
    .sub_slider {
        .rowbox.row {
            .two_slide.col-md-6 {
                .swiper.swiper_2 {
                    width: 18%;
                    height: 420px !important;
                }
            }

            .col-md-5.textbox {
                .npay {
                    display: none;
                } 
            }
        }

    }
}

@media (max-width:755px) {
    .sub_top {
        .col-md-3 {
            .iconz {
                font-size: 12px;
                & img {
                    width: 30px;
                    height: 30px;
                }
                p {
                    display: none;
                }
            }
            
        }
    }

    .sub_slider {
        .rowbox.row {
            .col-md-6 {
                .swiper.swiper_2 {
                    width: 17% !important;
                    height: 450px !important;
                }
            }
        }
    }
}

@media (max-width:810px) {
    .sub_slider {
        .rowbox.row {
            .col-md-6 {
                .swiper.swiper_2 {
                    width: 17% ;
                    height: 510px !important;
                }
            }
        }
    }
}

@media (max-width:930px) {
    .sub_slider {
        .rowbox.row {
            .col-md-6 {
                .swiper.swiper_2 {
                    width: 17% ;
                    height: 580px !important;
                }
            }
        }
    }
}


@media (max-width:1040px) {
    .sub_top {
        .col-md-3 {
            width: 100%;
        }
    }

    .sub_slider {
        .rowbox.row {
            .col-md-6 {
                .swiper.swiper_2 {
                    width: 18% !important;
                    height: 620px !important;
                }
            }
        }
    }
}


@media (max-width:1110px) {
    .sub_top {
        flex-direction: column;
    }

    .sub_slider {
        .rowbox.row {
            .col-md-6 {
                .swiper.swiper_2 {
