}


@media (max-width:1190px) {
    .sub_slider {
        .rowbox.row {
            justify-content: center;

            .col-md-6 {
                width: 90%;
                justify-content: center;
                margin-bottom: 50px;

                .swiper.swiper_2 { 
                    height: 650px;
                }
            }
            .col-md-5.textbox {
                width: 85%;

            }
        }
    }
}


@media (max-width:1380px) {
    .sub_slider {
        .rowbox.row {
            .col-md-6 {
                .swiper.swiper_2 {
                    width: 16.5%;
