.sub_slider {
    max-width: 1500px;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .top_name {
        width: 100%;
        margin-top: 0;
    }

    .rowbox {
        display: flex;
        justify-content: space-between;
        margin-top: 0;

        .col-md-6 {
            display: flex;
            height: 100%;

            .swiper.sub_slide {
                width: 75%;
                height: 100%;
                margin-left: 0px;
                margin-right: 10px;

                & img {
                    width: 100%;
                }
            }

            .swiper.swiper_2 {
                width: 15%;
                height: 450px;
                margin-left: 10px;
                margin-right: 10px;

                & img {
                    width: 100%;
                }

                .swiper-wrapper {
        
                    .swiper-backface-hidden .swiper-slide {
                        height: 125px !important;
                    }
                    .swiper-slide .swiper-slide-next {
                        height: 125px !important;
                    }
                    .swiper-slide .swiper-slide-active {
                        height: 125px !important;
                    }
                }
            }
        }

        .textbox {
            width: 45%;

            .box1 {
                width: 100%;
                height: 140px;
                border-bottom: 1px solid #eee;

                .name {
                    width: 100%;
                    height: auto;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    & img {
                        width: 19px;
                    }
                    & img:hover {
                        cursor: pointer;
                        opacity: 0.7;
                    }
                }

                .price {
                    width: 100%;
                    height: auto;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    p {
                        font-size: 13px;

                        b {
                            font-size: 28px;
                        }

                        span {
                            color: #b2b2b2;
                            text-decoration: line-through;
                            margin: 0 5px 0 5px;
                        }

                        button {
                            width: 17px;
                            height: 17px;
                            border: none;
                            background-color: #838383;
                            color: #fff;
                            margin: 0 5px 0 5px;
                            font-size: 10px;
                            font-weight: 600;
                        }
                        
                    }
                    & img:hover {
                        cursor: pointer;
                        opacity: 0.7;
                    }
                }

                .style_p {
                    text-align: left;
                    font-size: 12px;
                    margin-top: -10px;

                    button {
                        width: 38px;
                        height: 18px;
                        border: 1px solid #666;
                        background: none;
                        font-size: 10px;
                        margin-left: 5px;
                        text-align: center;
                    }
                }
            }

            .box2 {
                width: 100%;
                height: 70px;
                border-bottom: 1px solid #eee;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 14px;

                p {
                    width: 15%;
                    height: 10%;
                }

                .middle {
                    width: 75%;
                    text-align: left;
                    margin-left: 40px;
                }

                .last {
                    font-size: 12px;
                    text-decoration: underline;
                }
            }

            .box3 {
                width: 100%;
                height: 60px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                p {
                    height: 10%;
                    font-size: 14px;
                }

                .colorchip {
                    display: flex;
                    justify-content: space-evenly;
                    width: 40%;
                    height: auto;

                    .txt {
                        margin: 20px 28px 0 0px;
                        color: #b2b2b2;
                    }
                  
                    .border {
                        width: 25px;
                        height: 25px;
                        border-radius: 100%;
                        margin-top: 20px;
                        font-size: 13px;
                        margin-left: -5px;
                        margin-right: -5px;
                        border: 1px solid #fff !important;

                        & img {
                            width: 18px;
                            border-radius: 50%;
                            margin-top: 1px;
                            margin-left: 0.5px;  
                        }
                    }

                    .border.black {
                        border: 1px solid #000 !important;
                    }
                    .border:hover {
                        cursor: pointer;
                    }
                }

                .choice {
                    font-size: 13px;
                    color: #b2b2b2;
                }

                .btns {
                    display: flex;
                    height: 100%;
                    align-items: center;

                    .size {
                        width: 50%;
                        margin: -13px 10px 0 8px;
                        color: #b2b2b2;
                    }

                    .btn {
                        width: 40px;
                        height: 25px;
                        border: 1px solid #bebebe;
                        border-radius: 0;
                        margin: 0px 8px 0 20px;
                        font-size: 13px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }

                    .btn2 {
                        width: 55px;
                        height: 25px;
                        margin-top: 16px;
                        border: 1px solid #bebebe;
                        border-radius: 0;
                        font-size: 13px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }

                    .btn:hover,.btn2:hover {
                        cursor: pointer;
                    }
                }
      
            }

            .box4 {
                height: 220px;
                display: flex;
                flex-wrap: wrap;
                border-top: 1px solid #414141;

                .final {
                    width: 100%;
                    height: 20%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-top: 20px;
                    p {
                        b {
                            font-size: 27px;
                        }
                    }
                }
                .cart_buy {
                    width: 100%;
                    display: flex;
                    justify-content: space-between;

                    .cart {
                        width: 49%;
                        height: 60px;
                        background: none;
                        color: #000;
                        border-color: #666;
                        border-radius: 0;
                    }

                    .cart:hover {
                        background-color: #ebebeb;
                    }

                    .buy {
                        width: 49%;
                        height: 60px;
                        border-radius: 0;
                    }

                    .buy:hover {
                        background-color: #414141;
                    }
                }
                

                .store {
                    width: 100%;
                    height: 60px;
                    margin-top: 10px;
                    border-radius: 0;
                    font-size: 16px;
                    background-color: #acacac;
                    border: none;
                }

                .store:hover {
                    background-color: #c4c4c4;
                }
            }

            .npay {
                width: 50%;
                display: flex;
                flex-direction: column;
                border-top: 3px solid #000;
                margin-top: 20px;

                .npay_buy {
                    height: 55px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-top: 10px;
                    border-bottom: 1px solid #e1e1e1;

                    .npay_img {
                        width: 85px;
                        margin-left: 5px;
                        margin-bottom: 5px;
                    }
                    .npay_btn {
                        width: 155px;
                        height: 40px;
                        background-color: #00de5a;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin: 0 10px 5px 10px;

                        & img {
                            width: 70px;
                        }
                    }

                    .npay_btn2 {
                        width: 40px;
                        height: 40px;
                        border: 1px solid #666;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin: 0 10px 5px 10px;

                        & img {
                            width: 40px;
                        }
                    }

                    .npay_btn:hover, .npay_btn2:hover {
                        cursor: pointer;
                    }
                }

                .npay_slide {
                    width: 100%;
                    height: 10%;

                    .swiper-button-prev {
                        width: 20px;
                        height: 20px;
                        border: 1px solid #b3b3b3;
                        color: #a7a5a5 !important;
                        text-align: center;
                        margin: 1px;
                        margin-right: 5px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        left: auto !important;
                        top: 8%;
                        right: 8%;
                        &::after {
                            font-size: 14px;
                        }
                    }
            
                    .swiper-button-next {
                        width: 20px;
                        height: 20px;
                        border: 1px solid #b3b3b3;
                        color: #a7a5a5 !important;
                        text-align: center;
                        margin: 1px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        top: 8%;
                        right: 2% !important;
                        &::after {
                            font-size: 14px;
                        }
                    }

                    .swiper-slide {
                        position: relative;

                        p {
                            margin-top: 5px;
                            margin-left: 5px;
                            font-size: 12px;
                            text-align: left;
                            color: #919191;
                            span {
                                color: #20c000;
                            }
                        }
                    }
                } 

            }

            .explain {
                width: 100%;
                height: auto;

                .ex_title {
                    height: 60px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    border-top: 1px solid #dadada;
                    border-bottom: 1px solid #dadada;
                    h5 {
                        margin: 0px 0 0 7px;
                        font-size: 16px;
                        font-weight: 600;
                    }
                    p {
                        margin: 0px 7px 0 0px;
                        font-weight: 600;
                    }
                }

                .ex_title:hover {
                    cursor: pointer;
                }

                .ex_content {
                    height: 240px;
                    text-align: left;
                    font-size: 14px;
                    overflow-y: scroll;
                    display: none;

                    p {
                        margin-top: 25px;
                    }
                }

                .ex_content::-webkit-scrollbar {
                    width: 10px; 
                     /* 스크롤바의 색상 */
                    border-radius: 10px;
                }

                .ex_content::-webkit-scrollbar-track {
                    background: rgba(110, 110, 110, 0.185); 
                    border-radius: 10px;
                }

                .ex_content::-webkit-scrollbar-thumb {
                    background: #b3b3b3;
                    height: auto;
                    border-radius: 10px;
                }

                .ex_open {
                    display: block;
                }
            }

            .star {
                display: flex;
                align-items: center;
                justify-content: space-around;
                height: 75px;
                .rw {
                    width: 50%;
                    height: 20%;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;

                    p {
                        height: 10px;
                        font-size: 14px;
                        font-weight: 600;
                        color: #a0a0a0;
                        b {
                            text-decoration: underline;
                            color: #414141;
                            font-size: 15px;
                        }
                    }

                    & img {
                        width: 62px;
                        height: 13px;
                        margin-top: -2px;
                    }

                    p:hover {
                        cursor: pointer;
                    }

                    img:hover {
                        cursor: pointer;
                        opacity: 0.6; 
                    }
                }
                .share {
                    width: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;

                    p {
                        height: 10px;
                        font-size: 14px;
                        font-weight: 600;
                        color: #a0a0a0;
                    }

                    & img {
                        width: 20px;
                        margin-top: -10px;
                    }
                    .story img {
                        width: 27px;
                        height: 27px;
                        margin-top: -8px;
                    }

                    p:hover {
                        cursor: pointer;
                    }

                    img:hover {
                        cursor: pointer;
                        opacity: 0.6; 
                    }
                }
            }
        }

    }



}

.cody {
    max-width: 1500px;

    h5 {
        font-weight: 600;
        color: #4e4e4e;
    }

    .cody_slider {
        height: 35%;

        .cody_slide {
            margin-top: 35px;
            
           & img {
            width: 100%;
           }
           .swiper-slide {
            display: flex;
            flex-direction: column;
            align-items: center;
           }

           p {
            margin-top: 40px;
            font-weight: 600;
           }

           .swiper-button-prev {
            top: 40%;
            left: 0;
           } 

           .swiper-button-next {
            top: 40%;
            right: 0;
           }
        }

        .cart {
            width: 42%;
            height: 45px;
            background: none;
            color: #000;
            border-color: #666;
            border-radius: 0;
            font-size: 14px;
        }

        .cart:hover {
            background-color: #ebebeb;
        }
    }
}

.three_tab {
    margin-top: 20px;

    .nav-tabs {
        justify-content: center;

        .nav-item {
                margin: 0 10px 0 10px;
        
                .nav-tabs .nav-link {
                    border-bottom: 1px solid #eee;

                    .active {
                        border: none;
                    }
                }
        
                
            }
            
            
        
    }
    .tab-content {

        .now_detail {
           max-width: 1200px;

           a {

            & img {
                width: 100%;
            }
            
           }

            .details {
               width: 100%;
                height: auto;
                margin-top: 20px;
                & img {
                    width: 100%;
                }
            }
        }

        .delivery {
            width: 100%;
            margin-bottom: 50px;
            text-align: left;
            
            tr {
                th {
                        width: 200px;
                        padding: 20px 0 20px 0;
                        margin-right: 20px;
                        border: 1px solid #dfdede98;
                        text-align: center;
                        vertical-align: middle;
                        background-color: #f2f2f2;
                    }
                td {
                    border: 1px solid #dfdede98;
                    vertical-align: middle;
                    padding: 20px 0 20px 0;
                    width: 88%;
                    ul {
                        li {
                            list-style: circle;
                        }
                    }
                }
                span {
                    color: #b2b2b2;
                }
            }
        }

        @media (max-width:1030px) {
            .photo {
                .star_view {
                    .star_left.disnone {
                        width: 95%;
                        border-right: none;
                    }
                    .star_right {
                        display: none;
                    }
                }
            }
            .delivery {
                font-size: 12px;
            }
        }

        @media (max-width:1030px) {
            .photo {
                .star_view {
                    .star_left {
                        width: 60%;
                    }
                }
            }
        }

        .photo {
            width: 90%;

            .photo_img {
                width: 100%;
            }
            .photo_title {
                height: 70px;
                border-bottom: 2px solid #000;
                display: flex;
                justify-content: space-between; 
                align-items: center;
                p {   
                    font-weight: 600;
                    padding-top: 5px;
                    margin-bottom: -0.5rem;
                    span {
                        color: #acacac;
                        font-weight: 500;
                        cursor: pointer;
                    }

                }
            }  

            .star_view {
                width: 100%;
                height: 250px;
                border-bottom: 1px solid #dfdede98;
                display: flex;
                align-items: center;

                .star_left {
                    width: 30%;
                    height: 75%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    border-right: 1px solid #dfdede98;

                    & img {
                        width: 40px;
                        margin-right: 10px;
                    } 
                    span {
                        font-size: 40px;
                        font-weight: 600;
                    }
                    button {
                        width: 160px;
                        height: 45px;
                        font-size: 15px;
                    }
                    button:hover {
                        background-color: #666;
                    }
                }
                .star_right {
                    width: 70%;
                    height: 60%;
                    ul {
                        margin-top: 10px;
                            li {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                font-size: 13px;
                                height: 30px;
                    
                                p {
                                    width: 68px;
                                    margin-right: 10px;
                                }
                    
                                .bar {
                                    width: 67%;
                                    height: 11px;
                                    background-color: #000;
                                    border-radius: 10px;
                                    margin-left: 15px;
                                }
                                .bar2 {
                                    width: 67%;
                                    height: 11px;
                                    background-color: #e2e2e2b0;
                                    border-radius: 10px;
                                    margin-left: 15px;
                                }
                            }
                        }
                    
                }
                
            }

            .review_photo {
                height:auto;
                display: flex;
                justify-content: space-between; 
                align-items: center;
                flex-direction: column;
                border-bottom: 1px solid #dfdede98;

                .photo2 {
                    width: 100%;
                    height: 20%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                        p {
                            margin-top: 30px;
                        }
                    }
                
                .img_box {
                    display: flex;
                    margin-bottom: 30px;
                    
                        & img {
                            width: 100%;
                            padding: 5px;
                            border-radius: 10px;
                        }                          
                        & img:hover {
                            cursor: pointer;
                            opacity: 0.7;
                        }
                }
            } 
                
            .suggest {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                height: 60px;
                button {
                    height: 40px;
                    margin: 0 7px;
                    background: none;
                    border: 1px solid #dfdede98;
                }
            }

            .drop_box {
                display: flex;
                #dropdown-basic-button {
                        background: none;
                        border: none;
                        color: #000;

                    }
                .dropdown-menu{
                    a {
                        font-size: 14px;
                        & img {
                            width: 85px;
                        }
                        input {
                            margin-left: 20px;
                        }
                        span {
                            margin: 0 3px 0 0px;
                        }
                        .notgood {
                            margin-right: 16px;
                        }
                    }
                }
            }

            @media (max-width:945px) {
                .customer {
                    .zzim_rw.wid90 {
                        width: 95%;
                    }
                    .fiter {
                        display: none;
                    }
                }
            }

           


            .customer {
                width: 100%;
                height: 360px;
                display: flex;
                align-items: center;
                font-size: 14px;
                border-top: 1px solid #dfdede98;
                border-bottom: 1px solid #dfdede98;
                margin-left: 0;
                margin-top: 0;

                .zzim_rw {
                    height: 90%;
                    display: flex;
                    flex-direction: column;
                    text-align: left;
                    justify-content: center;

                    .p_btn:hover {
                        cursor: pointer;
                        background-color: #d1d1d15e;
                    }
                    
                    .p_btn {
                        width: 120px;
                        height: 30px;
                        border: 1px solid #929292;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 5px;
                        color: #666;
                    }
                    .date {
                        display: flex;
                        justify-content: space-between;
                        & img {
                            width: 100px;
                            margin-right: 10px;
                        }
                        .data {
                            margin-right: 30px;
                        }
                   } 
                   .cust_img {
                    
                    & img {
                        width: 130px;
                        margin-right: 10px;
                        border-radius: 5px;
                    }
                   }

                   .thumb {
                    width: 35%;
                    display: flex;
                    align-items: flex-start;
                    margin-top: 15px;
                    cursor: pointer;

                    & img {
                        width: 15px;
                        margin-right: 5px;
                    }
                    & img:hover {
                        cursor: pointer;
                        opacity: 0.7;
                    }
                    p {
                        font-size: 12px;
                        margin-right: 10px;
                    }
                   } 
                }  
            }

            .fiter {
                text-align: left;
                font-size: 13px;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                height: 73%;
                border-left: 1px solid #dfdede98;

                p {
                    margin-bottom: 0;
                    margin-left: 30px;
                }
            }

            .fiter.no_img {
                text-align: left;
                font-size: 13px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 73%;
                border-left: 1px solid #dfdede98;

                p {
                    margin-bottom: 0;
                    margin-left: 30px;
                }  
                .p_top {
                    margin-top: 35px;
                } 
            }
        }
    }
}

@media (max-width:775px) {
    .sub_top {
        .col-md-3 {
            width: 35%;
        }
    }
    .cody {
        .cody_slider {
            .cody_slide {
                font-size: 13px;
                .swiper-button-prev {
                    display: none;
               }
               .swiper-button-next {
                display: none;
           }
            }

        }
    }

    .photo.container {
        .photo_img {
            display: none;
        }
        .drop_box.size_drop {
            display: none;
        }
        .review_photo {
            .img_box {
                .hide_img {
                    display: none;
                }
            }
        }
    }
}

@media (max-width:768px) {
    .three_tab {
            .photo {
                .customer.row {
                    .zzim_rw {
                        .thumb {
                            width: 100%;
                        }  
                    }
                }
            }
        }
    }

@media (max-width:670px) {
    .three_tab {
            .photo {
                .customer.row {
                    .zzim_rw {
                        .cust_img {
                            display: none;
                        }
                    }
                }
            }
        }
    }
