
.roll {
    height: 60px;
    background-color: #1a1a1a;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    color: #fff;
    position: relative;

    .top {
        position: absolute;
        width: 10%;
        top: 17px;
        right: -2%;
    }

    .top:hover {
        cursor: pointer;
        opacity: 0.8;
    }

    .rolling {
        width: 390px;
        height: auto;
        // background-color: #c9c9c9;
        display: flex;
        align-items: center;
        position: relative;
        margin: 0px 0px;

        p {
            width: 15%;
            font-weight: 600;
            font-size: 17px;
            margin: 5px 0;
        }

        .mySwiper {
            width: 80%;
            height: 100%;
            margin-left: 0px;
            margin-right: 0px;

            .swiper-slide {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                text-align: left;
                font-size: 15px;
                color: #d4d4d4;
                margin-left: 5px;
            }
        }


        .swiper-button-prev {
            width: 20px;
            height: 20px;
            border: 1px solid #fff;
            background: none;
            color: #fff;
            text-align: center;
            margin: 1px;
            margin-right: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
            left: auto !important;
            top: 7%;
            right: 7%;
            &::after {
                font-size: 14px;
            }
        }

        .swiper-button-next {
            width: 20px;
            height: 20px;
            border: 1px solid #fff;
            background: none;
            color: #fff;
            text-align: center;
            margin: 1px;
            display: flex;
            align-items: center;
            justify-content: center;
            top: 7%;
            right: 1% !important;
            &::after {
                font-size: 14px;
            }
        }
    }

}

@media (max-width:850px) {
    .roll.wrap {
        flex-wrap: wrap;
        height: auto;
    }
}

@media (max-width:950px) {
    .roll.wrap {
        .top {
            display: none;
        }
    }
}

@media (max-width:1278px) {
    .footer.row {
       flex-direction: column;
       .col-md-2 {
         width: 95%;
       }
    }
}


.footer {
    display: flex;
    justify-content: center;

    .col-md-9{
        display: flex;
        flex-direction: column;
        font-size: 14px;
        font-weight: 600;

        .logo_f {
            margin-top: 40px;
            margin-left: 32px;
            & img {
                width: 170px;
                height: 17px;
            }
        }

        li {
            float: left;
            margin-right: 10px;
            color: #666666;
        }

        p {
            margin-left: 30px; 
            color: #b2b2b2;
            text-align: left;
            line-height: 160%;
        }

        .icons {
            width: 20%;
            margin: 10px 0 35px 22px;
            display: flex;

            & img {
                width: 20px;
                margin: 0 6px;
            }
        }

        .icons img:hover {
            opacity: 0.7;
        }
    }

    .col-md-2 {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        text-align: right;
        justify-content: flex-end;
        margin-right: 50px;
        font-weight: 600;
        color: #666666;
        font-size: 15px;

        p {
            width: 100%;
            margin-top: 40px;
            font-size: 22px;

            span {
                color: #b2b2b2;
                font-size: 13px;
            }
        }

        .list {
            height: 100%;

            li {
                margin: 5px 0;
            }
        }
    }

}

