:root {
    --swiper-theme-color: #fd7e14;
}


@media (max-width:1100px) {
    .new.arrivals {
        .mySwiper.slide2 {

            .swiper-button-prev {
                top: 38%;
                left: -1%;
            }
            .swiper-button-next {
                top: 38%;
                right: -1%;
            }
        }
    }
}

@media (max-width:850px) {
    .new.arrivals {
        .mySwiper.slide2 {

            .swiper-button-prev {
                display: none;
            }
            .swiper-button-next {
                display: none;
            }
        }
    }
}

.new {

    h2 {
        text-align: left;
    }

    .list {
        display: flex;
        justify-content: flex-end;
        font-size: 16px;

        p {
            margin: 0 5px;
            color: #b2b2b2;

            &.tbclick {
                color: #212529;

            }
        }
    }

    .list:hover {
        cursor: pointer;
    }

    .slide2:hover {
        cursor: pointer;
    }

        .mySwiper.slide2 {
            position: relative;
    
            & img {
                width: 100%;
                margin-bottom: 30px;
            }
    
            h6 {
                font-weight: 600;
            }
    
            p {
                font-weight: 600;
                color: #b01f00;
    
                span {
                    margin: 0 5px;
                    color: #777;
                    font-size: 14px;
                    text-decoration: line-through;
                }
            }
    
            .swiper-button-prev {
                top: 42%;
                left: -0.5%;
            }
    
            .swiper-button-next {
                top: 42%;
                right: -0.5%;
            }
    
            .swiper-slide {
                position: relative;
    
                .hover_sd {
                    // position: absolute;
                    top: 0;
                    left: 0;
                    // display: none;
                    transition: all 0.5s;
    
                    & img {
                        width: 100%;
    
                    }
    
                    &:hover .privew {
                        bottom: 40%;
                        z-index: 1;
                        opacity: 1;
                    }
    
                    .privew {
                        width: 100%;
                        height: 20%;
                        position: absolute;
                        bottom: 10%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-bottom: 55px;
                        opacity: 0;
                        transition: all 0.5s;
    
    
    
                        p {
                            width: 60px;
                            height: 60px;
                            background: #eee;
                            border-radius: 100%;
                            margin: 0 15px;
                            opacity: 0.7;
    
    
    
                            .priv {
                                width: 19px;
                                height: 19px;
                                margin: 21px 0;
                            }
                        }
    
                        p:hover {
                            opacity: 1;
                            background: #fff;
                            cursor: pointer;
                        }
                    }
    
    
                }
            }
        }
    }

    

    .tabclick {
        color: #212529;
    }

    .row {
        width: 100%;
        margin-bottom: 20px;
        margin-left: 0;

        .col-lg-4 {

            & img {
                width: 100%;
                // height: 100%;
            }

            .swiper-button-prev {
                top: 48%;
                left: 0;
            }

            .swiper-button-next {
                top: 48%;
                right: 0%;
            }
        }


    }

    @media (max-width:1450px) {
        .right_s {
            .top {
                h3 {
                    font-size: 25px;
                }
                h1 {
                    font-size: 35px; 
                }
            }
        }
    }

    
    @media (max-width:1270px) {
        .right_s {
            .top {
                h3 {
                    font-size: 20px;
                }
                h1 {
                    font-size: 30px; 
                }
            }

            .slide3 {
                margin-top: -30px;
            }
        }

    }

    .right_s {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        .top {

            margin-top: 40px;

            h3 {
                color: #b2b2b2;
            }
        }

        .s_title {
            width: 95%;
            margin-top: 5%;
            text-align: left;
            margin-left: 25px;

            h4 {
                font-size: 20px;
                font-weight: 600;
                margin-left: 15px;
            }
        }

        .slide3 {
            // position: absolute;
            width: 90%;
            height: 60%;
            bottom: 0;

            & img {
                width: 100%;
            }

            .swiperSlide {
                width: 94%;
                margin: 20px 0;
            }

            .swiper-button-prev {
                top: 52%;
                left: 1%;
                margin: -35px 0;
            }

            .swiper-button-next {
                top: 52%;
                right: 1%;
                margin: -35px 0;
            }

        }

        .slide3 img:hover {
            cursor: pointer;
            opacity: 0.6;
        }
    }
        
    

    .insta {
        margin: 50px 0;
        padding: 10px;

        a {
            text-decoration: none;
            color: #666;

        }

        a:hover {
            color: #333;
            text-decoration: underline;
            text-underline-offset: 5px;
        }
    }

    .instag {
        margin-top: 70px;

        .col-md-12 {
            display: flex;
            justify-content: space-between;
            margin-bottom: 20px;

            .peed {
                width: 290px;

                & img {
                    width: 100%;
                    height: 100%;
                }

                & img:hover {
                    cursor: pointer;
                    opacity: 0.7;
                }
            }

            .peed1 {
                width: 290px;
                margin-top: 15px;

                & img {
                    width: 100%;
                    height: 90%;
                }

                & img:hover {
                    cursor: pointer;
                    opacity: 0.7;
                }
            }
        }

    }




@media (max-width:768px) {
    .hover_sd {
        h6 {
        font-size: 13px;   
        }
        font-size: 12px;
        .privew.p_hover {  
            p {
               display: none;
            }       
               
        }
    }
}
