.cart {
    max-width: 1200px;

    table {
        border-top: 2px solid #000;
        vertical-align: middle;
    
        tr {
           
            th {
                width: 140px;
                height: 50px;
                font-size: 15px;
                color: #575757;
                vertical-align: middle;

                input {
                    accent-color: #000;
                }
            }
            .prod_name {
                width: 650px;
            }

            .del {
 
                .del_btn {
                    border: 1px solid #dad8d8;
                    font-size: 12px;
                    border-radius: 100%;
                    width: 25px;
                    height: 25px;
                    margin-left: 15px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .del_btn:hover {
                    cursor: pointer;
                    background-color: #eeeeee46;
                }
            }

            .produce_{       

                & img {
                    float: left;  
                    }                

                .ms-3 {
                    text-align: left;
                    display: flex;
                    align-items: flex-start;
                    justify-content: center;
                    flex-direction: column;
                    margin-top: 15px;
                    padding-left: 15px;

                    P {
                        color: #b2b2b2;
                        font-size: 14px;
                    }
                    .option {
                        width: 70px;
                        height: 30px;
                        color: #000;
                        border: 1px solid #dad8d8;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 13px;
                    }
                    .option:hover {
                        background: #ebebeb;
                        cursor: pointer;
                    }
                }
                
            }

            td {
            
                input {
                    accent-color: #000;
                }

                .plus {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-top: 15px;
                    

                    button {
                        border: 1px solid #ebebeb;
                        background: none;
                        font-size: 14px;
                        font-weight: 600;
                    }
                }

                .change {
                    margin-top: 10px;
                    font-size: 14px;
                    text-decoration: underline;
                    text-underline-offset : 5px;
                }

                p {
                    span {
                        color: #b2b2b2;
                        text-decoration: line-through;
                    }
                }
                
            }
        }
    }

    .btn_two {
        text-align: left;
        height: 100px;

        button {
            border: 1px solid #b2b2b2;
            width: 100px;
            height: 40px;
            margin-right: 10px;
            background: none;
            font-size: 14px;
            margin-top: 10px;
        }
        button:hover {
            cursor: pointer;
            background-color: #ebebeb7e;
        }
    }

    .payment {
        height: 180px;
        border: 1px solid;

        .pay_div {
            height: 95%;
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            margin: 10px auto;
            font-size: 15px;
        }
    }

    .btn_three {
        height: 150px;
        display: flex;
        align-items: flex-end;
        justify-content: center;  

        button {
            border: 1px solid #b2b2b2;
            width: 170px;
            height: 60px;
            margin-right: 10px;
            background: none;
            font-size: 16px;
            margin-top: 10px;
            margin-bottom: 20px;
        }
        button:hover {
            cursor: pointer;
            background-color: #ebebeb7e;
        }

        .bk_btn {
            width: 200px;
            background-color: #000;
            color: #fff;
        }
        .bk_btn:hover {
            background-color: #2c2c2c;
        }
    }

    .npay {
        width: 25%;
        display: flex;
        flex-direction: column;
        border-top: 3px solid #000;
        margin: 10px auto;

        .npay_buy {
            height: 55px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 10px;
            border-bottom: 1px solid #e1e1e1;

            .npay_img {
                width: 85px;
                margin-left: 5px;
                margin-bottom: 5px;
            }
            .npay_btn {
                width: 155px;
                height: 40px;
                background-color: #00de5a;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 10px 5px 10px;

                & img {
                    width: 70px;
                }
            }

            .npay_btn2 {
                width: 40px;
                height: 40px;
                border: 1px solid #666;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 10px 5px 10px;

                & img {
                    width: 40px;
                }
            }

            .npay_btn:hover, .npay_btn2:hover {
                cursor: pointer;
            }
        }

        .npay_slide {
            width: 100%;
            height: 10%;

            .swiper-button-prev {
                width: 20px;
                height: 20px;
                border: 1px solid #b3b3b3;
                color: #a7a5a5 !important;
                text-align: center;
                margin: 1px;
                margin-right: 5px;
                display: flex;
                align-items: center;
                justify-content: center;
                left: auto !important;
                top: 8%;
                right: 8%;
                &::after {
                    font-size: 14px;
                }
            }
    
            .swiper-button-next {
                width: 20px;
                height: 20px;
                border: 1px solid #b3b3b3;
                color: #a7a5a5 !important;
                text-align: center;
                margin: 1px;
                display: flex;
                align-items: center;
                justify-content: center;
                top: 8%;
                right: 2% !important;
                &::after {
                    font-size: 14px;
                }
            }

            .swiper-slide {
                position: relative;

                p {
                    margin-top: 5px;
                    margin-left: 5px;
                    font-size: 12px;
                    text-align: left;
                    color: #919191;
                    span {
                        color: #20c000;
                    }
                }
            }
        } 


    }

    .recommend {
        height: 550px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 20px 0 60px 0;

        h4 {
            text-align: left;
            height: 50px;
        }

        .commen_slide {
            width: 100%;
            height: 85%;

            & img {
                width: 100%;
            }

            .swiper-button-prev {
                top:39%;
               left: -1%;            
            }
            .swiper-button-prev::after {
                font-size: 35px;
            }
            .swiper-button-next {
                top:39%;
               right: -1%;            
             }
             .swiper-button-next::after {
                 font-size: 35px;
            }

            .swiper-slide {
                .hover_sd {
                    // position: absolute;
                    top: 0;
                    left: 0;
                    // display: none;
                    transition: all 0.5s;
    
                    & img {
                        width: 100%;
    
                    }
    
                    &:hover .privew {
                        bottom: 40%;
                        z-index: 1;
                        opacity: 1;
                    }
                    h6 {
                        margin-top: 30px;
                    }

                    p {
                        color: #b01f00;
                        span {
                            color: #b2b2b2;
                            text-decoration: line-through;
                            margin-left: 5px;
                            font-size: 14px;
                        }
                    }
    
                    .privew {
                        width: 100%;
                        height: 20%;
                        position: absolute;
                        bottom: 10%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-bottom: 55px;
                        opacity: 0;
                        transition: all 0.5s;
    
                        p {
                            width: 60px;
                            height: 60px;
                            background: #eee;
                            border-radius: 100%;
                            margin: 0 15px;
                            opacity: 0.7;
 
                            .priv {
                                width: 19px;
                                height: 19px;
                                margin: 21px 0;
                            }
                        }
    
                        p:hover {
                            opacity: 1;
                            background: #fff;
                            cursor: pointer;
                        }
                    }
    
    
                }
            }
        }
    }
}
